@font-face {
    font-family: 'Natasha';
    src: local('Natasha'),
        url(data:application/font-woff2;charset=utf-8;base64,) format('woff2'),
        url(data:application/font-woff;charset=utf-8;base64,) format('woff'),
        url('subset-Natasha.ttf') format('truetype'),
        url('subset-Natasha.svg#Natasha') format('svg');
    font-weight: bold;
    font-style: normal;
}

